.playlist-cover{padding:24px 0;display:grid;grid-template-columns:232px 1fr;grid-column-gap:24px;-webkit-column-gap:24px;column-gap:24px}.playlist-cover__imgbox{width:232px;height:232px}.playlist-cover__img{width:100%;height:100%;box-shadow:0 4px 60px rgba(0,0,0,.5)}.playlist-cover__info{display:flex;flex-direction:column;justify-content:flex-end}.playlist-cover__info--type{margin:4px 0 0;letter-spacing:-0.4px;font-size:12px;font-weight:700;line-height:16px;letter-spacing:.1em;text-transform:uppercase}.playlist-cover__info--title{display:-webkit-box;-webkit-line-clamp:1;-webkit-box-orient:vertical;overflow:hidden;margin:0;padding:.08em 0;font-size:72px;line-height:72px;font-weight:700;letter-spacing:-0.04em;text-transform:none;margin-top:8px}.playlist-cover__detail{display:flex;flex-direction:column;align-items:flex-start;margin-top:16px}.playlist-cover__detail--text{font-size:14px;line-height:16px;font-weight:400;letter-spacing:normal;text-transform:none;color:#b3b3b3;padding-bottom:8px}.playlist-cover__owner{display:flex;color:#b3b3b3;font-size:14px;line-height:16px;font-weight:400;letter-spacing:normal;text-transform:none}.playlist-cover__owner--info{color:#fff;font-weight:700;cursor:pointer}.playlist-cover__owner--info:hover{text-decoration:underline}.playlist-cover__owner--follower::before,.playlist-cover__owner--song::before{content:"•";margin:0px 4px}